/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { graphql } from 'gatsby';
import Page from 'src/layout/Page';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { MagazineContents } from 'src/models';
import { Color } from 'src/constants';
import Markdown from 'src/components/Markdown';
import { useSpring, animated } from 'react-spring';

export const pageQuery = graphql`
  query($path: String!) {
    mdx(frontmatter: { path: { eq: $path } }) {
      body
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
      }
    }
  }
`;

interface Props {
  data: {
    mdx: MagazineContents;
  };
}
const Magazine = ({ data }: Props) => {
  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(-30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
    config: {
      duration: 500,
    },
    delay: 400,
  });

  const { mdx } = data;
  if (!mdx) {
    return <div>Render Error</div>;
  }

  const { frontmatter, body } = mdx;

  return (
    <Page pageName={frontmatter.title} showHead={false}>
      <Container
        data-type="markdown"
        css={css`
          max-width: 800px;
          margin: 3rem auto;
        `}
      >
        <Row>
          <Col xs={12} sm={12} md={12} lg={12}>
            <TitleArea style={titleAnimation}>
              <Title>{frontmatter.title}</Title>
              <Date>{frontmatter.date}</Date>
            </TitleArea>
            <animated.div style={descriptionAnimation}>
              <Markdown contents={body} />
            </animated.div>
          </Col>
        </Row>
        <Row
          css={css`
            margin-top: 5rem;
          `}
        >
          <Col
            css={css`
              text-align: center;
            `}
          >
            <Button href="/magazines" variant="outline-secondary">
              <i
                css={css`
                  margin-right: 1rem;
                `}
                className="fas fa-book-open"
              ></i>
              다른 매거진 더 보러가기
            </Button>
          </Col>
        </Row>
      </Container>
    </Page>
  );
};

const TitleArea = styled(animated.div)`
  border-bottom: 1px solid ${Color.Grey100};
  margin-bottom: 2rem;
  padding-bottom: 2rem;
`;
const Title = styled.h1`
  font-weight: 700;
  color: ${Color.Grey900};
`;
const Date = styled.p`
  font-size: 1rem;
  color: ${Color.Grey800};
  margin: 0;
  font-family: serif;
`;

export default Magazine;
